'use strict';

define(['lib/merge'], function (merge) {

    "use strict";

    var settings = merge({}, {
        contact: 'lee.cooper@lski.uk',
        auth: {
            urls: {
                logout: '#logout',
                login: '#login'
            }
        },
        urls: {
            root: '/',
            api: '/'
        },
        router: {
            routes: [{ url: '', params: { page: 'home-page' } }, { url: 'contact', params: { page: 'contact-page' } }, { url: 'logout', params: { page: 'logout-page' } }, { url: 'login', params: { page: 'login-page' } }, { url: 'about', params: { page: 'about-page' } }, { url: 'blogs/:label:', params: { page: 'blog-page' } }, { url: 'blog/edit/{id}', params: { page: 'blog-edit-page' } }, { url: 'blog/new', params: { page: 'blog-create-page' } }, { url: 'blog/{id}', params: { page: 'blog-details-page' } }, { url: 'code', params: { page: 'code-page' } }, { url: 'privacy', params: { page: 'privacy-page' } }],
            components: [{ name: 'home-page', args: { require: 'components/home-page/vm' } }, { name: 'logout-page', args: { require: 'components/logout-page/vm' } }, { name: 'login-page', args: { require: 'components/login-page/vm' } }, { name: 'contact-page', args: { require: 'components/contact-page/vm' } }, { name: 'about-page', args: { require: 'components/about-page/vm' } }, { name: 'code-page', args: { require: 'components/code/index-page/vm' } }, { name: 'blog-page', args: { require: 'components/blog/index-page/vm' } }, { name: 'blog-hidden-page', args: { require: 'components/blog/hidden-blog-page/vm' } }, { name: 'blog-details-page', args: { require: 'components/blog/details-page/vm' } }, { name: 'blog-edit-page', args: { require: 'components/blog/edit-page/vm' } }, { name: 'blog-create-page', args: { require: 'components/blog/create-page/vm' } }, { name: 'header-nav', args: { require: 'components/shared/header-nav/vm' } }, { name: 'footer-nav', args: { require: 'components/shared/footer-nav/vm' } }, { name: 'summary', args: { template: { require: 'text!components/shared/summary/view.html' } } }, { name: 'quick-links', args: { template: { require: 'text!components/shared/quick-links/view.html' } } }, { name: 'side-bar', args: { require: 'components/shared/side-bar/vm' } }, { name: 'privacy-page', args: { template: { require: 'text!components/privacy/view.html' } } }, { name: 'file-browser', args: { require: 'components/shared/file-browser/vm' } }]
        },
        fileBrowser: {
            browserUrl: '/browser/index.htm'
        }
    }, _settings());

    return settings;

    /**
    * Gets the provided basic settings for this application from the view
    */
    function _settings() {

        try {

            // Check to see if any settings have come from the server
            var scriptTag = document.querySelector('script[data-app]'),
                raw = scriptTag && scriptTag.getAttribute('data-app'),
                settings = raw && JSON.parse(raw) || {};

            return settings;
        } catch (e) {

            throw new Error('There was an error extracting the settings for the application');
        }
    }
});